.mainBox {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 50%;
    left: 50%;
    transform:translate(-50%, -50%);
    border-radius: 16px;
    background: url('./Bg1.png');
}

.textBox {
    padding: 30px 30px 0px 30px;
    color: #ffffff;
}

.textBox p {
    font-size: 20px;
}

.contactsBox {
    display: flex;
}

.stackBox {
    display: flex ;
    padding: 0px 30px;
}

.iconBackground {
    width: 100px;
    height: 100px;
    background-color: #ffffff;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 15px;
}

.projectBox {
    color: #ffffff;
    padding: 0px 30px 50px 30px;
}

.projectBtn {
    box-shadow:inset 0px 1px 0px 0px #fce2c1;
	background:linear-gradient(to bottom, #ffc477 5%, #fb9e25 100%);
	background-color:#ffc477;
	border-radius:11px;
	border:2px solid #eeb44f;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-size:16px;
	font-weight:bold;
	padding:8px 18px;
	text-decoration:none;
	text-shadow:0px 1px 0px #cc9f52;
}

.projectBtn:hover {
	background:linear-gradient(to bottom, #fb9e25 5%, #ffc477 100%);
	background-color:#fb9e25;
}
.projectBtn:active {
	position:relative;
	top:1px;
}

.closeBtn {
    position: absolute;
    left: 92%;
    top: 10px;
}

.closeBtn:hover {
    position: absolute;
    left: 92%;
    top: 11px;
}

